import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Contacts extends Component {

  constructor(props) {
    super(props);
    this.state = {
      contacts: []
    };
  }

  componentDidMount() {
    axios.get('/contacts')
      .then(res => {
        this.setState({ contacts: res.data });
        console.log(this.state.contacts);
      });
  }

  render() {
    return (
      <div class="container">
        <div class="panel panel-default">
          <div class="panel-heading">
          <br></br>
            <h3 class="panel-title">
              Users
            </h3>
          </div>
          <div class="panel-body">
            <Link to="/create"><button class="my-2 mx-2 btn btn-primary">Create User</button></Link>
            <Link to="/"><button class="my-2 btn btn-info">Return to Dashboard</button></Link>

            <table class="table table-stripe">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Address</th>
                </tr>
              </thead>
              <tbody>
                {this.state.contacts.map(c =>
                  <tr>
                    <td><Link to={`/show/${c.id}`}>{c.name}</Link></td>
                    <td>{c.address}</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
        <div class="ml-2"><small>End of list</small></div>
      </div>
    );
  }
}

export default Contacts;