import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';
import axios from 'axios';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      contacts: []
    };
  }

  componentDidMount() {
    axios.get('/contacts')
      .then(res => {
        this.setState({ contacts: res.data });
        console.log(this.state.contacts);
      });
  }

  render() {
    return (
      <div class="container">
        <div class="panel panel-default">
          <div class="panel-heading">
            <br></br>
            <h3 class="panel-title">
              Task Dashboard
            </h3>
          </div>
          <div class="panel-body">
            <Link to="/create"><button class="my-2 btn btn-primary mx-2">Add Task</button></Link>
            <Link to="/task"><button class="my-2 mx-1 btn btn-info">View Tasks</button></Link>
            <Link to="/create"><button class="my-2 mx-1 btn btn-info">Create User</button></Link>
            <Link to="/contact"><button class="my-2 mx-1 btn btn-info">View Users</button></Link>
            <table class="table table-stripe">
              <thead>
                <tr>
                  <th>Item</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                {this.state.contacts.map(c =>
                  <tr>
                    <td><Link to={`/show/${c.id}`}>{c.name}</Link></td>
                    <td>{c.address}</td>
                  </tr>
                )}
              </tbody>
            </table>
            <div class="ml-2"><small>End of list</small></div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;